<script>
import guid from './utils/guid'
const mapData = {
  A1: '德(好)',
  A2: '德(较好)',
  A3: '德(一般)',
  A4: '德(差)',
  B1: '能(好)',
  B2: '能(较好)',
  B3: '能(一般)',
  B4: '能(差)',
  C1: '勤(好)',
  C2: '勤(较好)',
  C3: '勤(一般)',
  C4: '勤(差)',
  D1: '绩(好)',
  D2: '绩(较好)',
  D3: '绩(一般)',
  D4: '绩(差)',
  E1: '廉(好)',
  E2: '廉(较好)',
  E3: '廉(一般)',
  E4: '廉(差)',
  F1: '综合评价(优秀)',
  F2: '综合评价(称职)',
  F3: '综合评价(基本称职)',
  F4: '综合评价(不称职)'
}

export default {
  name: 'App',
  data () {
    return {
      input: '', // 导入或输入的名字数据
      list: [],
      beforeInfo: '姓名---评级---增减分数'
    }
  },

  methods: {
    // 导入名字
    addNames () {
      const str = this.input.trim()
      let list = []
      if (!str) return
      this.$confirm('确认要导入姓名么?', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const arr = str.split(' ')
        list = arr.map((item, index) => ({
          order: guid(),
          name: item,
          A1: 0,
          A2: 0,
          A3: 0,
          A4: 0,
          B1: 0,
          B2: 0,
          B3: 0,
          B4: 0,
          C1: 0,
          C2: 0,
          C3: 0,
          C4: 0,
          D1: 0,
          D2: 0,
          D3: 0,
          D4: 0,
          E1: 0,
          E2: 0,
          E3: 0,
          E4: 0,
          F1: 0,
          F2: 0,
          F3: 0,
          F4: 0
        }))
        this.list = [...this.list, ...list]
        this.saveData2LocalStorage()
        this.input = ''
      }).catch(() => { })
    },

    // 单元格单击时
    cellClick (row, column, cell, event) {
      // console.log(row, column, cell, event)
      // console.log(row.name, row.order, column.property, event.ctrlKey)
      const list = [...this.list]
      const rowItem = list.find(item => item.order === row.order)

      if (column.property === 'name') return
      if (event.ctrlKey) {
        // 减分
        if (rowItem[column.property] <= 0) return
        rowItem[column.property]--
        this.beforeInfo = `${row.name}---${mapData[column.property]}---减1分`
        this.saveData2LocalStorage()
        this.$message({ message: `${row.name}---${mapData[column.property]}---减1分  操作成功`, type: 'error' })
      } else {
        // 加分
        rowItem[column.property]++
        this.beforeInfo = `${row.name}---${mapData[column.property]}---加1分`
        this.saveData2LocalStorage()
        this.$message({ message: `${row.name}---${mapData[column.property]}---加1分  操作成功`, type: 'success' })
      }
      this.list = list
      this.$forceUpdate()
    },

    // 双击删除
    celldblclick (row, column, cell, event) {
      if (column.property === 'name') {
        // 删除操作
        this.$confirm(`确定要删除 ${row.name} 么?`, '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.list = this.list.filter(item => item.order !== row.order)
          this.saveData2LocalStorage()
        }).catch(() => { })
      }
    },

    // 保存数据到本地
    saveData2LocalStorage () {
      localStorage.setItem('list', JSON.stringify(this.list))
    },

    // 导出表格
    exportEXCEL () {
      // 获取表格
      var exportFileContentHeader = document.querySelector('.el-table__header').outerHTML
      var exportFileContentBody = document.querySelector('.el-table__body').outerHTML
      // 使用Blob
      var blob = new Blob([exportFileContentHeader, exportFileContentBody], {
        // type: 'text/plain;charset=utf-8'
        type: 'application/vnd.ms-excel'
      })
      // 解决中文乱码问题
      // blob = new Blob([String.fromCharCode(0xfeff), blob], { type: blob.type })
      // 设置链接
      var link = window.URL.createObjectURL(blob)
      var a = document.createElement('a') // 创建a标签
      const d = new Date()
      const year = d.getFullYear()
      const month = d.getMonth() + 1
      const day = d.getDate()
      // console.log(year, month, day)
      a.download = `投票${year}-${month}-${day}.xls` // 设置被下载的超链接目标（文件名）
      a.href = link // 设置a标签的链接
      document.body.appendChild(a) // a标签添加到页面
      a.click() // 设置a标签触发单击事件
      document.body.removeChild(a) // 移除a标签
    },

    // 清空表格数据
    clearData () {
      const res = confirm('大米, 你确定要清空数据么?')
      if (!res) return
      this.list = []
      localStorage.removeItem('list')
    }
  },

  // 钩子函数
  created () {
    // 获取本地数据
    const list = JSON.parse(localStorage.getItem('list')) || []
    this.list = list
  },

  updated () {
    console.log('updated')
  }
}
</script>

